'use strict';

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import Routine from './routine.js';
import Module from './module.js';


// MAIN //

/**
* WebAssembly routine to compute the variance of a double-precision floating-point strided array ignoring `NaN` values and using Welford's algorithm.
*
* @name dnanvariancewd
* @type {Routine}
*
* @example
* import Float64Array from '@stdlib/array-float64';
*
* // Define a strided array:
* var x = new Float64Array( [ 1.0, -2.0, NaN, 2.0 ] );
*
* // Perform operation:
* var v = dnanvariancewd.main( 4, 1, x, 1 );
* // returns ~4.3333
*
* @example
* import Float64Array from '@stdlib/array-float64';
*
* // Define a strided array:
* var x = new Float64Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0, NaN, NaN ] );
*
* // Perform operation:
* var v = dnanvariancewd.ndarray( 5, 1, x, 2, 1 );
* // returns 6.25
*/
var dnanvariancewd = new Routine();
setReadOnly( dnanvariancewd, 'Module', Module.bind( null ) );
dnanvariancewd.initializeSync(); // eslint-disable-line node/no-sync


// EXPORTS //

export default dnanvariancewd;
